}
.navLink span {
  -webkit-transition: all 0.3s;
  transition: all 0.3s;
}
.navLink::before {
  content: ' ';
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 1;
  opacity: 0;
  -webkit-transition: all 0.3s;
  transition: all 0.3s;
